import { h } from "@stencil/core";
export class RaulPagingBar {
    constructor() {
        this.entities = [10, 20, 30, 40, 50];
        this.totalRows = 0;
        this.rowsPerPage = 10;
        this.currentPage = 1;
    }
    currentPageChanged(_, oldVal) {
        this.validateCurrentPage(oldVal);
    }
    rowsPerPageChanged() {
        this.validateRowsPerPage();
    }
    componentWillLoad() {
        this.validateRowsPerPage();
        this.validateCurrentPage();
    }
    decrement() {
        this.currentPage = this.currentPage - 1;
        this.updateBar('previousPage');
    }
    entitiesChange(event) {
        this.rowsPerPage = Number(event.target.value);
        this.currentPage = 1;
        this.updateBar('entitiesChange');
    }
    increment() {
        this.currentPage = this.currentPage + 1;
        this.updateBar('nextPage');
    }
    pageValueChange(event) {
        let val = Number(event.target.value);
        if (val > this.totalPages() || val < 1 || isNaN(val)) {
            event.target.value = this.currentPage;
        }
        else {
            this.currentPage = val;
            this.updateBar('pageValueChange');
        }
        event.target.blur();
    }
    totalPages() {
        return Math.ceil(this.totalRows / this.rowsPerPage);
    }
    startRow() {
        return (this.currentPage - 1) * this.rowsPerPage + 1;
    }
    endRow() {
        const endRow = (this.currentPage - 1) * this.rowsPerPage + this.rowsPerPage;
        return endRow <= this.totalRows ? endRow : this.totalRows;
    }
    updateBar(event) {
        if (this.validateRowsPerPage()) {
            this.pagingChange.emit({
                event: event,
                currentPage: this.currentPage,
                rowsPerPage: this.rowsPerPage,
                totalPages: this.totalPages(),
                startRow: this.startRow(),
                endRow: this.endRow()
            });
        }
    }
    validateCurrentPage(oldVal = 1) {
        if (this.currentPage < 1 || this.currentPage > this.totalPages()) {
            console.error(`RAUL Paging Bar Component ERROR: current page can't be less than 1 or greater than total pages number`);
            this.currentPage = oldVal;
        }
    }
    validateRowsPerPage() {
        if (!this.entities.includes(this.rowsPerPage)) {
            console.error(`RAUL Paging Bar Component ERROR: rowsPerPage does not match entities array. rowsPerPage is set to ${this.rowsPerPage} but available entities are: ${this.entities.toString()}`);
        }
        return this.entities.includes(this.rowsPerPage);
    }
    render() {
        return (h("div", { class: "r-paging-bar" },
            h("div", { class: "r-paging-bar__col r-paging-bar__col--entries" },
                "Show",
                h("div", { class: "r-paging-bar__entry-select" },
                    h("select", { onChange: (event) => this.entitiesChange(event) }, this.entities && this.entities.map(entity => (h("option", { value: entity, selected: this.rowsPerPage === entity }, entity)))),
                    h("raul-icon", { class: "r-select__arrow", icon: "arrow-down-v" })),
                "entries"),
            h("div", { class: "r-paging-bar__col r-paging-bar__col--count" },
                this.startRow(),
                " - ",
                this.endRow(),
                " of ",
                this.totalRows),
            h("div", { class: "r-paging-bar__col r-paging-bar__col--nav" },
                h("button", { type: "button", "aria-label": "Previous Entries", onClick: () => this.decrement(), disabled: this.currentPage === 1 },
                    h("raul-icon", { icon: "arrow-left-v" })),
                h("div", { class: "r-paging-bar__nav" },
                    "Page",
                    h("div", { class: "r-paging-bar__input" },
                        h("input", { type: "number", min: "1", max: this.totalPages(), value: this.currentPage, onChange: (event) => this.pageValueChange(event), onKeyPress: (event) => event.key === 'Enter' ? this.pageValueChange(event) : null })),
                    "of ",
                    this.totalPages()),
                h("button", { type: "button", "aria-label": "Next Entries", onClick: () => this.increment(), disabled: this.currentPage === this.totalPages() },
                    h("raul-icon", { icon: "arrow-right-v" })))));
    }
    static get is() { return "raul-paging-bar"; }
    static get originalStyleUrls() { return {
        "$": ["raul-paging-bar.scss"]
    }; }
    static get styleUrls() { return {
        "$": ["raul-paging-bar.css"]
    }; }
    static get properties() { return {
        "entities": {
            "type": "any",
            "mutable": false,
            "complexType": {
                "original": "any",
                "resolved": "any",
                "references": {}
            },
            "required": false,
            "optional": false,
            "docs": {
                "tags": [],
                "text": ""
            },
            "attribute": "entities",
            "reflect": true,
            "defaultValue": "[10, 20, 30, 40, 50]"
        },
        "totalRows": {
            "type": "number",
            "mutable": false,
            "complexType": {
                "original": "number",
                "resolved": "number",
                "references": {}
            },
            "required": false,
            "optional": false,
            "docs": {
                "tags": [],
                "text": ""
            },
            "attribute": "total-rows",
            "reflect": true,
            "defaultValue": "0"
        },
        "rowsPerPage": {
            "type": "number",
            "mutable": false,
            "complexType": {
                "original": "number",
                "resolved": "number",
                "references": {}
            },
            "required": false,
            "optional": false,
            "docs": {
                "tags": [],
                "text": ""
            },
            "attribute": "rows-per-page",
            "reflect": true,
            "defaultValue": "10"
        },
        "currentPage": {
            "type": "number",
            "mutable": true,
            "complexType": {
                "original": "number",
                "resolved": "number",
                "references": {}
            },
            "required": false,
            "optional": false,
            "docs": {
                "tags": [],
                "text": ""
            },
            "attribute": "current-page",
            "reflect": true,
            "defaultValue": "1"
        }
    }; }
    static get events() { return [{
            "method": "pagingChange",
            "name": "pagingChange",
            "bubbles": true,
            "cancelable": true,
            "composed": true,
            "docs": {
                "tags": [],
                "text": ""
            },
            "complexType": {
                "original": "any",
                "resolved": "any",
                "references": {}
            }
        }]; }
    static get watchers() { return [{
            "propName": "currentPage",
            "methodName": "currentPageChanged"
        }, {
            "propName": "rowsPerPage",
            "methodName": "rowsPerPageChanged"
        }]; }
}
